
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'

export default {

directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Blur,
Tab,
TabItem,
XAddress
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
mounted () {
this.failureShowToast = false
this.changeSuccessShow = false
this.getQuestion()
this.$vux.loading.show({
text: 'loading'
})
},
data () {
return {
title: '问题',
ownPic: '../assets/dialog/01.jpg',
url: '../assets/dialog/01.jpg',
questionTitle: '',
wrongCounts: 0,
rightCounts: 0,
increaseCredits: 0,
deductibleCredits: 0,
answers: '',
showConfirmBox: false,
computeDaysValue: '',
questionData: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
failureText: ''
}
},
methods: {
goRedeemDetail () {
this.$router.push('setting')
},
goStruction () {
this.$router.push('setting')
},
goQuestion () {
this.showConfirmBox = true
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {
},

onConfirm () {
this.$router.push('payment')
},
onShow () {

},
onHide () {

},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
getQuestion () {
let questionId = this.$route.query.questionId
this.$http.post(this.$apis.questionById, {questionId: questionId}).then(res => {
if (res.code === 200) {

this.clock = res.data.clock  
this.blankNum = res.data.blankNum
this.wrongCounts = res.data.wrongCounts
this.rightCounts = res.data.rightCounts
this.questionTitle = res.data.title
this.questionData = res.data.sentence
this.answers = res.data.answer.join('  , ')
this.deductibleCredits = res.data.deductibleCredits
this.increaseCredits = res.data.increaseCredits
this.$vux.loading.hide()
} else {
this.questionData = '<p>Today\'s question is not ready, please wait for a minute!</p>'
this.$vux.loading.hide()
}
}).catch(function (error) {
console.log('Error: ' + error)
this.$vux.loading.hide()
})
}
}
}



